function create_contract(c_no, name, description, actioned, supplier, remarks) {
    const today = new Date().toDateString()
    return {
        c_no,
        name,
        description,
        signed: today,
        expiry: today,
        actioned,
        supplier,
        remarks,
    }
}

export const dummy_contracts = Array(15).fill(
    create_contract(
        "001",
        "Microsoft dynamics implementation",
        "Implementation of all modules for ERP",
        "DDICT",
        "Dynasoft",
        "In Progress"
    )
)
