import React from "react"
import styled from "styled-components"
import banner from "../banner.png"
import {
    Table,
    TableBody,
    TableHead,
    Paper,
    Toolbar,
    TableRow,
    TableCell,
    Link,
    TextField,
    Button,
    Dialog,
    DialogContentText,
    DialogTitle,
    DialogContent,
    DialogActions,
} from "@material-ui/core"
import { dummy_contracts } from "../dummy"
import { EditContract } from "./edit_contract"

export class Contracts extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            editing: false,
        }
    }
    handleClickOpen() {
        this.setState({ editing: true })
    }
    handleClickClose() {
        this.setState({ editing: false })
    }
    render() {
        const { editing } = this.state
        const headers = [
            "No.",
            "Number",
            "Name",
            "Description",
            "Date Signed",
            "Expiry  Date",
            "Actioned by",
            "Supplier",
            "Remarks",
            "Actions",
        ]
        return (
            <ContractsContainer>
                <nav>
                    <img src={banner} />
                    <span>Contracts</span>
                </nav>
                <Paper>
                    <StyledToolBar>
                        <TextField className="search" label="Search" />
                        <StyledButton variant="contained" color="secondary">
                            Export
                        </StyledButton>
                        <StyledButton
                            variant="contained"
                            color="secondary"
                            onClick={this.handleClickOpen.bind(this)}>
                            Add Contracts
                        </StyledButton>
                        <StyledButton variant="contained" color="secondary">
                            Manage Users
                        </StyledButton>
                    </StyledToolBar>
                    <Table>
                        <TableHead>
                            <TableRow>
                                {headers.map(header => (
                                    <TableCell key={header}>{header}</TableCell>
                                ))}
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {dummy_contracts.map((contract, index) => (
                                <TableRow key={index}>
                                    <TableCell>{index + 1}</TableCell>
                                    {Object.values(contract).map(value => (
                                        <TableCell key={value}>
                                            {value}
                                        </TableCell>
                                    ))}
                                    <TableCell>
                                        <Actions>
                                            <Link href="#">View</Link>
                                            <Link href="#">Download</Link>
                                        </Actions>
                                    </TableCell>
                                </TableRow>
                            ))}
                        </TableBody>
                    </Table>
                </Paper>
                <Dialog
                    open={editing}
                    onClose={this.handleClickClose.bind(this)}>
                    <DialogTitle>Create Contract</DialogTitle>
                    <DialogContent>
                        <EditContract />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClickClose.bind(this)}>
                            Cancel
                        </Button>
                        <Button variant="contained" color="secondary">
                            Save
                        </Button>
                    </DialogActions>
                </Dialog>
            </ContractsContainer>
        )
    }
}

const ContractsContainer = styled.div`
    max-width: 100vw;
    display: flex;
    flex-direction: column;
    nav {
        background-color: #1e73be;
        padding: 5px;
        display: flex;
        color: #fff;
        font-size: 24px;
        align-items: center;
        span {
            flex-grow: 1;
            text-align: center;
            margin-left: -160px;
        }
        img {
            height: 50px;
            width: auto;
        }
    }
`

const Actions = styled.span`
    display: flex;
    flex-direction: column;
`

const StyledToolBar = styled(Toolbar)`
    display: flex;
    height: 40px !important;
    align-items: flex-end;
    .search {
        flex-grow: 1;
    }
`

const StyledButton = styled(Button)`
    margin: 5px !important;
`
